@charset "UTF-8";
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select {
  margin: 0;
  font-family: inherit;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}

img {
  display: block;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

@font-face {
  font-family: "Open Sans";
  src: local("../../assets/font/OpenSans.ttf");
  font-family: "Open Sans italic";
  src: local("../../assets/font/OpenSans-Italic.ttf");
}
html {
  font-size: 16px;
}

body {
  font-family: "Open Sans";
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--background);
  width: 100%;
  height: 100%;
  margin-top: 4.3775rem;
  overflow-x: hidden;
  color: var(--primary-color);
}

.body_noscroll {
  overflow-y: hidden;
}

#root {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

hr {
  width: 100%;
  color: var(--grey);
  height: 1px;
  margin: 0.4rem 0;
  opacity: 0.5;
}

.section-heading {
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.login-button {
  display: inline-block;
  width: 100%;
  background-color: var(--pale-violet);
  border: none;
  cursor: pointer;
  color: var(--background);
  border-radius: 0.25rem;
  transition: 0.1s ease;
  padding: 0.5rem;
  font-weight: 600;
  text-align: center;
  font-size: 0.875rem;
}
.login-button:hover, .login-button:active {
  background-color: var(--background2);
  color: var(--primary-color);
  transition: 0.1s ease;
}

.logout-button {
  width: 100%;
  background-color: var(--red);
  border: none;
  cursor: pointer;
  color: var(--background2);
  border-radius: 0.25rem;
  transition: 0.1s ease;
  padding: 0.5rem;
  font-weight: 600;
  text-align: center;
  font-size: 0.875rem;
}
.logout-button:hover, .logout-button:active {
  background-color: var(--background2);
  color: var(--primary-color);
  transition: 0.1s ease;
}

.lists-buttons {
  display: none;
  position: absolute;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: max-content;
  bottom: 0;
  left: 0;
  border-radius: 0 0.25rem 0 0;
  background-color: var(--background2);
  padding: 0.3rem;
  gap: 0.3rem;
}
.lists-buttons button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  margin: 0.3rem;
  cursor: pointer;
}
.lists-buttons button > img {
  width: 18px;
  height: 18px;
}

.theme-switcher {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  font-weight: 600;
  font-size: 0.815rem;
  flex-shrink: 0;
}
.theme-switcher__icon {
  filter: invert(46%) sepia(9%) saturate(842%) hue-rotate(193deg) brightness(95%) contrast(94%);
}
.theme-switcher .checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  border-radius: 1rem;
  width: 3rem;
  height: 2rem;
  padding: 0.3rem;
  background-color: var(--background2);
  transition: 0.2s ease-out;
  border: 2px solid var(--border-grey);
}
.theme-switcher .checkbox__slider {
  width: 50%;
  height: 100%;
  background-color: var(--grey);
  border-radius: 1rem;
  transform: translateX(0);
}
.theme-switcher .checkbox_active {
  background-color: var(--backlight);
  transition: 0.2s ease-out;
}
.theme-switcher .checkbox_active .checkbox__slider {
  transform: translateX(100%);
  background-color: var(--pale-violet);
}

.list-error {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--primary-color);
}

.profile-avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 2px solid var(--border-grey);
  width: 90px;
  height: 90px;
  font-weight: 700;
  font-size: 2.812rem;
  text-transform: capitalize;
  color: var(--pale-violet);
  user-select: none;
  background-color: var(--background-light);
}

.header-profile__profile-avatar {
  width: 36px;
  height: 36px;
  font-size: 1.125rem;
}

.profile-info__profile-avatar {
  width: 36px;
  height: 36px;
  font-size: 1.125rem;
}

.profile-page-header__profile-avatar {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  margin-top: -45px;
}

.dropdown-menu {
  position: relative;
  display: flex;
  align-items: center;
  background-color: var(--background2);
  border-radius: 0.25rem;
  padding: 0.4rem;
  cursor: pointer;
}
.dropdown-menu__placeholder {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
  font-size: 0.9rem;
  user-select: none;
}
.dropdown-menu__placeholder::after {
  display: inline-block;
  content: "";
  width: 16px;
  height: 16px;
  margin-left: auto;
  background-image: url("/public/assets/icons/expand.svg");
  filter: invert(99%) sepia(0%) saturate(3780%) hue-rotate(104deg) brightness(113%) contrast(100%);
  background-repeat: no-repeat;
}
.dropdown-menu_active .dropdown-menu__placeholder::after {
  background-image: url("/public/assets/icons/expanded.svg");
}

.option-list {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  margin-top: 2.5rem;
  background-color: var(--background2);
  border-radius: 0.25rem;
  padding: 0.5rem 0;
}
.option-list__element {
  padding: 0.5rem;
  font-weight: 600;
  font-size: 0.9rem;
}
.option-list__element:hover, .option-list__element:active {
  background-color: var(--background-light);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loading__animation {
  max-width: 150px;
  max-height: 150px;
  min-height: 30px;
  min-width: 30px;
  border-top: 8px solid var(--pale-violet);
  border-radius: 50%;
  animation: spin 2s linear infinite;
}

.auth-alert {
  text-align: center;
}
.auth-alert__message {
  font-weight: 700;
  margin-bottom: 0.7rem;
}

.header {
  position: fixed;
  width: 100%;
  height: 4.3775rem;
  top: 0;
  left: 0;
  padding: 0 1.25rem;
  background-color: var(--background2);
  z-index: 10;
}
.header-container {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.header-container__logo {
  position: absolute;
  display: block;
  left: 50%;
  transform: translateX(-50%);
  align-self: center;
  height: 90%;
}
.header-container__logo img {
  height: 100%;
}
.header-container__profile {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.header-container__logout-button:hover, .header-container__logout-button:active {
  background-color: var(--pale-violet);
}
.header-nav {
  display: none;
}
.header-nav ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}
.header-nav ul li {
  font-weight: 600;
  font-size: 0.8rem;
  transition: 0.05s ease-out;
}
.header-nav ul .container {
  padding: 0.3rem;
  position: relative;
  border-radius: 0.25rem 0.25rem 0 0;
  flex-shrink: 0;
}
.header-nav ul .container:hover {
  background-color: var(--grey);
  border-bottom: none;
  color: var(--background);
}
.header-nav ul .container:hover .header-profile__dropdown {
  display: flex;
}
.header-nav nav > ul li:not(.container) {
  margin-top: 2px;
}
.header-nav nav > ul li:not(.container) a {
  display: inline-block;
}
.header-nav nav > ul li:not(.container) a::after {
  display: block;
  content: "";
  border-bottom: 2px solid var(--pale-violet);
  transform: scaleX(0);
  transition: transform 0.15s ease-in-out;
}
.header-nav nav > ul li:not(.container) a:hover::after, .header-nav nav > ul li:not(.container) a:active::after {
  transform: scaleX(1);
}
.header-nav__login-button:hover, .header-nav__login-button:active {
  background-color: var(--red);
}
.header-profile {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.header-profile__logout-button:hover, .header-profile__logout-button:active {
  background-color: var(--pale-violet);
}
.header-profile__dropdown {
  display: none;
  flex-direction: column;
  position: absolute;
  width: 100%;
  height: auto;
  top: 100%;
  left: 0;
  background-color: var(--grey);
  border-radius: 0 0 0.25rem 0.25rem;
  padding-bottom: 0.5rem;
  width: 100%;
  line-height: 1.25rem;
  text-align: center;
  color: var(--background);
  transition: background-color 0.1s;
}
.header-profile__dropdown a:hover {
  background-color: var(--primary-color);
  transition: background-color 0.1s;
}
.header__theme-switcher .theme-switcher__label {
  margin-right: 0.3rem;
}
.header__theme-switcher .theme-switcher__icon {
  margin-right: 0.2rem;
}

.bottom-nav {
  position: fixed;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-direction: row;
  width: 100%;
  height: 3.375rem;
  bottom: 0;
  left: 0;
  padding: 0 4.19rem;
  gap: 1rem;
  background-color: var(--background2);
  transform: translateY(0);
  transition: transform 0.1s ease;
}
.bottom-nav__button {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}
.bottom-nav__button .container::after {
  display: block;
  content: "";
  border-bottom: 2px solid var(--pale-violet);
  transform: scaleX(0);
  transition: transform 0.2s ease-out;
}
.bottom-nav__button .container img {
  filter: invert(99%) sepia(88%) saturate(2%) hue-rotate(92deg) brightness(113%) contrast(100%);
  opacity: 0.5;
  padding-bottom: 0.2rem;
}
.bottom-nav__button_active .container > img {
  filter: invert(77%) sepia(58%) saturate(3045%) hue-rotate(207deg) brightness(103%) contrast(93%);
  opacity: 1;
}
.bottom-nav__button_active .container::after {
  transform: scaleX(1);
}
.bottom-nav_hidden {
  transform: translateY(100%);
}

.footer {
  width: 100%;
  background-color: var(--background-light);
}
.footer .footer-list {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  padding: 0.25rem 0;
}
.footer .footer-list__element {
  font-weight: 700;
  margin-right: 0.5rem;
  font-size: 0.8rem;
  line-height: 1rem;
}
.footer .footer-list__element a:hover {
  color: var(--pale-violet);
}
.footer .footer-grid {
  display: flex;
  align-items: center;
  margin-bottom: 0.75rem;
}
.footer p {
  font-size: 0.75rem;
}
.footer p a {
  text-decoration: underline;
}
.footer p a:hover {
  color: var(--pale-violet);
}
.footer .container {
  max-width: 950px;
  margin-right: auto;
  margin-left: auto;
  padding: 1.5rem 1.25rem 1.5rem 1.25rem;
}

.film-link {
  position: relative;
  border-radius: 0.25rem;
  border: 1px solid var(--border-grey);
  width: fit-content;
  overflow: hidden;
}
.film-link:hover, .film-link:active {
  border-color: var(--primary-color);
}

.home {
  position: relative;
  display: flex;
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: column;
  padding: 0 1.25rem;
}
.home__heading {
  font-family: inherit;
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.25rem;
  margin-top: 16.5rem;
  text-align: center;
}
.home__heading > span {
  color: var(--pale-violet);
}
.home__heading_authorized {
  margin-top: 1.31rem;
  text-align: left;
}
.home__greeting {
  font-size: 0.875rem;
  min-width: 11rem;
  margin-bottom: 1.31rem;
}
.home__greeting_unauthorized {
  margin-top: 1.2rem;
  text-align: center;
  margin-bottom: 2rem;
}
.home__banner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-height: min-content;
  background-color: var(--primary-color);
  margin: 2.6rem 0 3rem 0;
  border-radius: 0.25rem;
  overflow: hidden;
}
.home__banner img {
  object-fit: contain;
}
.home-unauthorized {
  position: absolute;
  top: 0;
  left: 0;
  max-height: 250px;
  height: 250px;
  width: 100%;
  background-image: url("/public/assets/home/home-background.jpg");
  background-position: center;
  background-size: cover;
}
.home__login-button {
  width: 50%;
  padding: 0.7rem;
}
.home-features {
  margin: 2.6rem 0 3rem 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.home-features .home-feature {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 2rem 1rem;
  gap: 1rem;
  color: var(--primary-color);
  border-radius: 0.25rem;
  background-color: var(--background2);
  transition: 0.1s ease;
}
.home-features .home-feature__icon {
  filter: invert(100%) sepia(0%) saturate(7491%) hue-rotate(196deg) brightness(99%) contrast(107%);
  width: 25px;
  max-width: 25px;
}
.home-features .home-feature__text {
  font-weight: 600;
}
.home-features .home-feature:hover, .home-features .home-feature:active {
  background-color: var(--red);
  color: var(--background2);
  transition: 0.1s ease;
}
.home-features .home-feature:hover .home-feature__icon, .home-features .home-feature:active .home-feature__icon {
  filter: invert(8%) sepia(7%) saturate(4062%) hue-rotate(196deg) brightness(91%) contrast(88%);
}

.popular-month__heading, .latest-news__heading, .recent-articles__heading, .popular-month-lists__heading {
  font-family: inherit;
  font-weight: 600;
  margin-bottom: 0.5rem;
}
.popular-month-section {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  gap: 0.3rem;
  font-weight: 700;
}
.popular-month-section__film-link {
  width: 32%;
}
.popular-month-section__film-link > .lists-buttons {
  display: flex;
}
.popular-month-lists-grid {
  display: flex;
  flex-direction: column;
  gap: 0.9rem;
}
.popular-month-lists-grid__element {
  width: 100%;
}
.popular-month-lists-grid__element h3 {
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 1.25rem;
}
.popular-month-lists-grid__element .container {
  display: flex;
  width: fit-content;
  justify-content: flex-start;
  flex-wrap: nowrap;
  height: auto;
  border-radius: 0.25rem;
  border: 1px solid var(--grey);
  overflow: hidden;
  margin-bottom: 0.25rem;
}
.popular-month-lists-grid__element .container:hover {
  border-color: var(--primary-color);
}
.popular-month-lists-grid__element .container__film-link {
  width: 16.666%;
}
.popular-month-lists-grid__element .container__film-link:hover > .lists-buttons {
  display: none;
}
.recent-articles .container {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
}

.article-card {
  min-height: 15rem;
  margin-right: 0.75rem;
  min-width: 15rem;
  background-color: var(--background2);
  border-radius: 0.25rem;
}
.article-card > div {
  padding: 0.75rem;
}
.article-card > div a:hover, .article-card > div:active {
  color: var(--pale-violet);
}
.article-card__heading {
  font-weight: 700;
  margin-bottom: 0.5rem;
  font-size: 1.25rem;
  line-height: 1.75rem;
}
.article-card__image {
  width: 100%;
  height: 8.4375rem;
  border-radius: 0.25rem 0.25rem 0 0;
}
.article-card__link {
  display: block;
  margin-top: 2rem;
  font-size: 0.8rem;
}
.article-card__description {
  font-size: 0.8rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 6;
  overflow: hidden;
}

.latest-news {
  margin-bottom: 1.25rem;
}
.latest-news-card {
  display: flex;
  flex-direction: column;
  border-radius: 0.25rem;
  background-color: var(--background2);
}
.latest-news-card__container {
  padding: 0.5rem 0.5rem 0.5rem 0.75rem;
}
.latest-news-card__heading {
  font-weight: 700;
  font-size: 1.25rem;
  margin-top: 0.5rem;
  letter-spacing: 0.05rem;
}
.latest-news-card__heading a:hover, .latest-news-card__heading:active {
  color: var(--pale-violet);
}
.latest-news-card__image {
  border-radius: 0.25rem 0.25rem 0 0;
}
.latest-news-card__link {
  text-transform: uppercase;
  margin-top: 0.25rem;
}
.latest-news-card__link:hover, .latest-news-card__link:active {
  color: var(--pale-violet);
}
.film {
  display: flex;
  flex-direction: column;
  min-height: 90vh;
}
.film-background {
  background-size: cover;
  background-repeat: no-repeat;
  height: 250px;
  max-height: 250px;
}
.film-page {
  display: flex;
  flex-direction: column;
  padding: 0 1.25rem;
}
.film-page-heading {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  flex-wrap: wrap;
  gap: 0.5rem;
}
.film-page-heading__title {
  font-weight: 600;
  line-height: 2.25rem;
  font-size: 2.25rem;
}
.film-page-heading__production {
  display: flex;
  flex-direction: row;
  gap: 0.25rem;
  font-size: 1rem;
}
.film-page-heading__production .year {
  font-style: italic;
  text-decoration: underline;
}
.film-page-heading__production .director span {
  text-decoration: underline;
  font-style: italic;
}
.film-page-heading__rating {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 1.8rem;
}
.film-page-heading__rating_good {
  color: var(--green);
}
.film-page-heading__rating_medium {
  color: var(--yellow);
}
.film-page-heading__rating_bad {
  color: var(--red);
}
.film-page-info__slogan {
  padding-top: 0.75rem;
  text-transform: uppercase;
  font-weight: 500;
}
.film-page-info__cast > p {
  font-size: 1rem;
  font-weight: 700;
  padding-bottom: 0.25rem;
}
.film-page-info__cast .container {
  display: flex;
  flex-wrap: wrap;
  gap: 0.25rem;
}
.film-page-info__cast .container p {
  padding: 0.25rem;
  line-height: 1rem;
  text-transform: capitalize;
  background-color: var(--background2);
  border-radius: 0.25rem;
}
.film-page-info__description {
  padding-top: 0.75rem;
}
.film-cover {
  position: relative;
  width: 7rem;
  height: 10rem;
  margin-top: -20%;
  flex-shrink: 0;
  overflow: hidden;
  border-radius: 0.25rem;
  border: 1.5px solid var(--border-grey);
  box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.0509803922);
}
.film-cover__lists-buttons {
  display: flex;
}
.film-review-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.5rem;
  margin-bottom: 1.25rem;
}
.film-review-form__title {
  border-radius: 0.25rem;
  border: none;
  background-color: var(--background-light);
  padding: 0.4rem;
  font-size: 0.9rem;
  color: var(--primary-color);
}
.film-review-form__title:focus {
  outline: none;
  background-color: var(--background2);
}
.film-review-form__title::placeholder {
  color: var(--primary-color);
  font-weight: 600;
}
.film-review-form__input {
  border-radius: 0.25rem;
  background-color: var(--background-light);
  padding: 0.4rem;
  border: none;
  width: 100%;
  height: 5rem;
  color: var(--primary-color);
  resize: none;
  font-size: 0.9rem;
}
.film-review-form__input:focus {
  outline: none;
  background-color: var(--background2);
}
.film-review-form__input::placeholder {
  font-weight: 600;
  color: var(--primary-color);
}
.film-review-form__submit {
  border-radius: 0.25rem;
  padding: 0.8rem;
  background-color: var(--background2);
  color: var(--primary-color);
  width: 50%;
  font-weight: 700;
  align-self: center;
  border: none;
