:root {
  --primary-color: #fff;
  --background: #24273a;
  --pale-violet: #c6a0f6;
  --background2: #1e2030;
  --grey: #6e738d;
  --background-light: #363a4f;
  --border-grey: #494d64;
  --backlight: rgba(237, 135, 150, 0.05);
  --red: #ed8796;
  --pink: #ee99a0;
  --green: #a6da95;
  --teal: #8bd5ca;
  --yellow: #eed49f;
  --peach: #f5a97f;
}

[data-theme=light] {
  --primary-color: #24273a;
  --background: #eff1f5;
  --pale-violet: #5b6078;
  --background2: #ccd0da;
  --grey: #9ca0b0;
  --background-light: #dce0e8;
  --border-grey: #bcc0cc;
  --backlight: rgba(237, 135, 150, 0.05);
  --red: #d20f39;
  --pink: #e64553;
  --green: #40a02b;
  --teal: #179299;
  --yellow: #df8e1d;
  --peach: #fe640b;
}
[data-theme=light] .header-profile__logout-button {
  color: var(--background);
}
[data-theme=light] .header-profile__logout-button:hover, [data-theme=light] .header-profile__logout-button:active {
  color: var(--background);
}
[data-theme=light] .header-profile__login-button {
  color: var(--primary-color);
}
[data-theme=light] .header-profile__login-button:hover, [data-theme=light] .header-profile__login-button:active {
  color: var(--background);
}
[data-theme=light] .home-features .home-feature {
  color: var(--primary-color);
}
[data-theme=light] .home-features .home-feature__icon {
  filter: invert(8%) sepia(9%) saturate(4273%) hue-rotate(194deg) brightness(94%) contrast(82%);
}
[data-theme=light] .home-features .home-feature:hover, [data-theme=light] .home-features .home-feature:active {
  color: var(--background);
